<template>
  <div>
    <div class="pagebar-title">新增农产品参数</div>

    <div class="module">
      <div>网关</div>
      <el-table :data="Main.tableData" style="width: 100%" border>
        <el-table-column prop="id" label="系统ID" width="100" align="center"> </el-table-column>

        <el-table-column prop="name" label="农产品名称">
        </el-table-column>

        <el-table-column label="更多" fixed="right" width="125" align="center">
          <template slot-scope="scope">
            <el-button @click="selF(scope.row)">选择</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="module">
      <div>从机</div>
      <el-table :data="Sub.tableData" style="width: 100%" border>
        <el-table-column prop="id" label="系统ID" width="100" align="center"> </el-table-column>

        <el-table-column prop="name" label="农作物名称">
        </el-table-column>

        <el-table-column prop="gbcode" label="农作物国家编码">
        </el-table-column>

        <el-table-column label="更多" fixed="right" width="125" align="center">
          <template slot-scope="scope">
            <el-button @click="selS(scope.row)">选择</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="module">
      <div class="mtitle">设备类型</div>
      <el-select v-model="form.quipment_type" placeholder="请选择设备类型">
        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>
    </div>

    <div class="module">
      <el-button @click="submit()">添加</el-button>
      <div class="r">
        <router-link :to="{path:'/iot/product/detail',  query:{product_id: this.product_id, principal_id: this.principal_id}}">
          <el-button>返回</el-button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { Query } from "@/api/gateway";
import { Bodylist } from "@/api/equipment";
import { Productobjbindaddoredit } from "@/api/product";
export default {
  name: "gatewayAdd",
  data() {
    return {
      drawer: false,
      form: {
          
      },
      Main: {
        form: {
          keyword: "",
          querypage: 1,
        },
        tableData: [], // 列表
        total: 0, // 总条目
        totalPage: 0, // 总页数
      },
      Sub: {
        form: {
          keyword: "",
          querypage: 1,
        },
        tableData: [], // 列表
        total: 0, // 总条目
        totalPage: 0, // 总页数
      },
      options: [
        {
          value: '1',
          label: '从机'
        },
        {
          value: '2',
          label: '摄像头'
        },
      ]
    };
  },
  components: {},
  methods: {
    submit() {
      this.$http(Productobjbindaddoredit(this.form), (res) => {
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            type: "success",
          });
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
    getMainlist() {
      this.$http(
        Query({
          principal_id: this.principal_id,
        }),
        (res) => {
          if (res.code == 200) {
            this.$message({
              message: res.msg,
              type: "success",
            });
            this.Main.total = res.data.total;
            this.Main.form.querypage = res.data.currentPage;
            this.Main.totalPage = res.data.totalPages;
            this.Main.tableData = res.data.result;
          } else {
            this.$message({
              message: res.msg,
              type: "error",
              duration: 5 * 1000,
            });
          }
        }
      );
    },
    selF(item) {
      console.log(item);
      this.Main.form.id = item.id;
      this.getSublist();
    },
    selS(item) {
      this.form.equipmentbody_id = item.id;
      this.form.product_id = this.product_id;
      this.$message({
        message: "选择从机成功",
        type: "success",
      });
    },
    getSublist() {
      this.$http(
        Bodylist({
          gatewayid: this.Main.form.id,
          querypage: this.currentPage,
        }),
        (res) => {
          if (res.code == 200) {
            this.$message({
              message: res.msg,
              type: "success",
            });
            this.Sub.total = res.data.total;
            this.Sub.form.querypage = res.data.currentPage;
            this.Sub.totalPage = res.data.totalPages;
            this.Sub.tableData = res.data.result;
          } else {
            this.$message({
              message: res.msg,
              type: "error",
              duration: 5 * 1000,
            });
          }
        }
      );
    },
  },
  created() {
    this.product_id = this.$route.query.product_id;
    this.principal_id = this.$route.query.principal_id;
    this.getMainlist();
  },
};
</script>
<style lang="scss" scoped>
</style>